.clear{
    
    display: flex;
    justify-content: center; /* Center horizontally */
    align-items: center; /*Center vertically*/
    margin: 1rem auto;
    width: 20%;
    padding: 10px;
    background: linear-gradient(45deg,rgba(0, 230, 255, 0.4), rgba(0, 220, 255, 0.2));
    border: none;
    background-color: #efefef;
    color: white;
    font-size: 18px;
    border-radius: 10px;
    cursor: pointer;
    box-shadow: 0px 0px 8px #888;
    transition: background 0.3s ease, transform 0.2s ease, box-shadow 0.3s ease;
    font-weight: bold;
  }
  
  .clear:hover {
    background: linear-gradient(45deg, rgba(255, 0, 0, 0.2), rgba(220, 0, 0, 0.1));
    transform: translateY(-2px);
    box-shadow: 0px 0px 8px #888;
    background-color: white;
  }